<template>
  <div>
    <div class="place-for-chart">
      <ChartButtons @selectPeriod="selectPeriod" v-if="this.coinName" />
      <vue-highcharts type="stockChart" v-if="showChart" :options="chartOptions" :redrawOnUpdate="true"
        :oneToOneUpdate="false" :animateOnUpdate="true" @rendered="onRender" />
    </div>
  </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import VueHighcharts from 'vue3-highcharts';
import HighCharts from 'highcharts';
import StockCharts from 'highcharts/modules/stock';
import boost from "highcharts/modules/boost";
import { ref, computed } from 'vue';
import ChartButtons from '@/components/ChartButtons.vue';
import dateTimeFilter from '@/filters/date.filter'
import localizeFilter from '@/filters/localize.filter'

StockCharts(HighCharts);
boost(HighCharts);

HighCharts.setOptions({
  colors: ['#28BEB2']
});


export default defineComponent({
  props: {
    coinName: {
      type: String,
      required: true
    },
    coinTicker: {
      type: String,
      required: true
    },
    animateChart: {
      type: Boolean,
      default: false
    },
    businessName: {
      type: String,
      required: true
    },
    businessId: {
      type: Number,
      required: true
    },
  },
  components: {
    VueHighcharts, ChartButtons
  },
  watch: {
    animateChart(newVal, oldVal) {
      if (newVal == true && oldVal == false) {
        this.$nextTick(this._animateChart())
      }
    },
    coinName(newVal, oldVal) {
      if (newVal != oldVal) {
        this.$nextTick(this.coinChanged())
      }
    },
    businessName(newVal, oldVal) {
      if (newVal != oldVal) {
        this.$nextTick(this.businessChanged())
      }
    },
  },
  data: () => ({
    showChart: true,
    startTime: null,
    endTime: null,
  }),
  setup(props) {
    // const seriesData = ref([])
    const seriesData = ref([[+new Date(), 1], [+new Date() + 1 * 24 * 60 * 60 * 1000, 2], [+new Date() + 2 * 24 * 60 * 60 * 1000, 10], [+new Date() + 3 * 24 * 60 * 60 * 1000, 26], [+new Date() + 4 * 24 * 60 * 60 * 1000, 85], [+new Date() + 5 * 24 * 60 * 60 * 1000, 60], [+new Date() + 6 * 24 * 60 * 60 * 1000, 177]]);
    // const seriesData = ref([])
    const chartOptions = computed(() => ({
      chart: {
        type: 'area',
        zoomType: 'x',
        styledMode: true
      },
      boost: { enabled: true },
      title: {
        text: '',
      },
      // plotOptions: { // not working
      //   series: {
      //       color: '#28BEB2',
      //       shadow: {
      //       color: '#28BEB2',
      //       opacity: 1
      //       }
      //   },
      // },
      defs: {
        gradient0: {
          tagName: 'linearGradient',
          id: 'gradient-0',
          x1: 0,
          y1: 0,
          x2: 0,
          y2: 1,
          children: [{
            tagName: 'stop',
            offset: 0
          }, {
            tagName: 'stop',
            offset: 1
          }]
        }
      },
      legend: {
        enabled: false
      },
      navigator: {
        xAxis: {
          type: 'datetime',
          labels: {
            format: '{value:%e %b}',
            align: 'center',
            rotation: 0
          }
        },
      },
      xAxis: {
        type: 'datetime',
        labels: {
          format: '{value:%e %b}',
          align: 'center',
          y: 30,
          rotation: 0
        }
      },
      yAxis: {
        labels: {
          align: 'left',
          x: 10,
          y: 0,
          format: '{text} USD'
        },
        title: {
          text: ''//props.coinName + ' in USD'
        },
      },
      colors: ['#28BEB2'],
      series: [{
        name: props.coinName,
        data: seriesData.value,
        // fillColor: { // not working
        //   linearGradient: [0, 0, 0, 300],
        //     stops: [
        //         [0, HighCharts.getOptions().colors[0]],
        //         [1, HighCharts.color(HighCharts.getOptions().colors[0]).setOpacity(0).get('rgba')]
        //     ]
        // }
      },
      ],
      tooltip: {
        formatter: function () {
          return '<div style="font-size:10;">' + dateTimeFilter(this.x, 'date') + '</div>' + '<br><div style="margin-top: 5px; margin-bottom: 0;"><b>1 ' + props.coinTicker + ' = ' + this.y + ' USD</b></div>'
        },
      },
    }));

    const onRender = () => {
      // // console.log('Chart rendered');
    };

    return {
      chartOptions,
      onRender,
    };
  },
  methods: {
    coinChanged() {
      if (this.coinName != null && this.coinName != undefined) {
        this.selectDefaultPeriod()
        this.loadData()
      }
    },
    businessChanged() {
      if (this.businessName != null && this.businessName != undefined) {
        this.selectDefaultPeriod()
        this.loadData()
      }
    },
    _animateChart() {
      setTimeout(() => {
        this.showChart = false;
        this.$nextTick(() => {
          this.showChart = true;
        });
      }, 1000)
    },
    selectDefaultPeriod() { // 1 month
      const endDate = new Date()
      const startDate = new Date()
      this.$utils.addMonths(startDate, -1)
      this.startTime = startDate.getTime()
      this.endTime = endDate.getTime()
    },
    selectPeriod(startTime, endTime) {
      this.startTime = startTime
      this.endTime = endTime
      this.loadData()
    },
    async loadData() {
      if (this.coinTicker) {
        try {
          const data = { coinTicker: this.coinTicker, startTime: this.startTime, endTime: this.endTime }
          const serverChartData = await this.$store.dispatch('getCoinPriceChartData', data)
          const newData = []
          serverChartData.forEach(d => {
            newData.push([d.date, d.value])
          })

          // for (let i = 0; i < 28; i++) {
          // newData.push([d.date + i*1000*60*60*24, d.value])
          // }
          const newSeries = [{ name: this.chartOptions.series[0].name, data: newData }]
          this.chartOptions.series = newSeries
          this.showChart = false;
          this.$nextTick(() => {
            this.showChart = true;
          });
        }
        catch (e) {
          this.$error(e)
          // console.log(e)
        }
      } else if (this.businessId) {
        try {
          const data2 = { businessId: this.businessId, startTime: this.startTime, endTime: this.endTime }
          const serverChartData2 = await this.$store.dispatch('getBusinessChartData', data2)
          const newData2 = []
          serverChartData2.forEach(d => {
            newData2.push([d.date, d.value])
          })
          // for (let i = 0; i < 28; i++) {
          // newData.push([d.date + i*1000*60*60*24, d.value])
          // }
          const newSeries = [{ name: this.chartOptions.series[0].name, data: newData }]
          this.chartOptions.series = newSeries
          this.showChart = false;
          this.$nextTick(() => {
            this.showChart = true;
          });
        }
        catch (e) {
          this.$error(e)
          // console.log(e)
        }
      }

    },
    onUpdated() {
    },
  },
  async mounted() {
    this.coinChanged()
    this.businessChanged()
  },
})
</script>

<style scoped>
.place-for-chart {
  position: relative;
}

@media(max-width: 768px) {
  .place-for-chart {
    margin: 20px 20px 0 20px;
    background: rgba(0, 0, 0, 0);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }
}

@media(min-width: 768px) {
  .place-for-chart {
    margin: 20px 0 10px 0;
    background: rgba(0, 0, 0, 0);
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-start;
  }
}

.chart-buttons {
  position: absolute;
  top: -54px;
  right: 0;
}

.place-for-trial-button {
  height: 100px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

@media (max-width:767.98px) {
  .chart-buttons {
    left: -17px;
  }
}
</style>